<template>
  <v-container class="fill-height" fluid>
    <div class="text-center">
      <v-snackbar v-model="snackbar" :timeout="timeout">
        {{ text }}
        <template v-slot:action="{ attrs }">
          <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">Close</v-btn>
        </template>
      </v-snackbar>
    </div>
    <v-row align="center" justify="center">
      <v-simple-table>
        <template v-slot:default>
          <thead>
            <tr>
              <th class="text-left">No</th>
              <th class="text-left">Title</th>
              <th class="text-left">Url</th>
              <th class="text-left">Thumb</th>
              <th class="text-left">Image</th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="(item, index) in result"
              :key="item.id"
              @click="add(item.id, item.title, item.url, item.thumbnailUrl)"
            >
              <td>{{ index + 1 }}</td>
              <td>{{ item.title }}</td>
              <td>{{ item.url }}</td>
              <td>{{ item.thumbnailUrl }}</td>
              <td>
                <v-card flat tile class="d-flex">
                  <v-img
                    :src="`${item.thumbnailUrl}`"
                    :lazy-src="`${item.thumbnailUrl}`"
                    max-height="50"
                    max-width="50"
                    aspect-ratio="1"
                    class="grey lighten-2"
                  >
                    <template v-slot:placeholder>
                      <v-row class="fill-height ma-0" align="center" justify="center">
                        <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
                      </v-row>
                    </template>
                  </v-img>
                </v-card>
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </v-row>
    <v-row align="center" justify="center">
      <v-btn medium color="primary" @click="loadmore()">Load More</v-btn>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: "Table",
  components: {},
  data: () => ({
    snackbar: false,
    text: "Ditambahkan Di Menu List Item",
    timeout: 2000,
    result: null,
    initlimit: 10,
    plus: 100,
  }),
  methods: {
    getdata() {
      this.isLoading(true);
      const baseURI = `https://jsonplaceholder.typicode.com/photos?_limit=${this.initlimit}`;
      this.$axios.get(baseURI).then((result) => {
        this.result = result.data;
        this.isLoading(false);
      });
    },
    loadmore() {
      this.initlimit = this.initlimit + this.plus;
      this.getdata();
    },
    add(id, title, url, thumbnailUrl) {
      this.$store.commit("addlist", {
        id: id,
        title: title,
        url: url,
        thumbnailUrl: thumbnailUrl,
      });
      this.input = {};
      this.snackbar = true;
    },
    isLoading(param) {
      this.$store.commit("isLoading", param);
    },
  },
  mounted() {
    this.getdata();
  },
};
</script>

